import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';
import { ArticulosPage } from '../pages/articulos/articulos';
import { BibliotecasPage } from '../pages/bibliotecas/bibliotecas';
import { RepositoriosPage } from '../pages/repositorios/repositorios';
import { BuscadoresPage } from '../pages/buscadores/buscadores';
import { HerramientasPage } from '../pages/herramientas/herramientas';


@NgModule({
  declarations: [
    MyApp,
    HomePage,
    TabsPage,
    ArticulosPage,
    BibliotecasPage,
    RepositoriosPage,
    BuscadoresPage,
    HerramientasPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    TabsPage,
    ArticulosPage,
    BibliotecasPage,
    RepositoriosPage,
    BuscadoresPage,
    HerramientasPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



